.Navbar {
    width: 100%;
    padding: 0.5rem 0;
    background-color: #1C3643;
  }
  
  .Navbar__brand {
    color: #ffffff;
  
    display: -webkit-flex;
  
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .Navbar__brand:hover {
    color: #ffffff;
