    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    margin-bottom: 1rem;
  }
.Badges__list-container {
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.10);
    display: -webkit-flex;
    display: flex;
    margin-top: 8px;
    margin-bottom: 8px;
    background-color: white;
}

.Badges__display-image {
    -webkit-align-self: center;
            align-self: center;
    margin-left: 4px;
    margin-right: 0px;
    margin-bottom: 4px;  
}

.Badges__display-image img {
    border-radius: 48px;
}

.Badges__display-content {
    -webkit-align-self: left;
